import Icon from "../Icon/Icon";
import { inject, observer } from "mobx-react";
import useLang from "../../hooks/useLang";
import Content from '../../components/Content/Content';

import './footer.less'
import { useEffect, useState } from "react";


function Footer({ intl }) {
  const lang = useLang(intl.dict, 'footer');
  const [year, setYear] = useState("")
  useEffect(() => {
    let date = new Date()
    let year = date.getFullYear()
    setYear(year)
  }, [])

  console.log("footer", lang);
  return (
    <div className='footer'>
      <div className='hr'></div>
      <div className='footer-box'>
        <div className='left'>
          <a rel='norelerror' className='one-a' href='https://deri.io/#/lite/trade/futures'>{lang['app']}</a>
          <a rel='norelerror' href='https://docs.deri.io/'>{lang['docs']}</a>
          <a rel='norelerror' href='https://misc.deri.io/#/team'>{lang['team']}</a>
          <a rel='norelerror' href='https://docs.deri.io/library/faqs'>{lang['faq']}</a>
        </div>
        <div className='banquan pc-bn'>© {year} {lang['deri-protocol']}</div>
        <div className='contact pc-con'>
          <div className='visit'>{lang['visit-us-on']}</div>
          <a target="_blank" rel='noreferrer' href="mailto: service@deri.finance"><Icon token='email' width='16' height='16' /></a>
          <a
            target="_blank"
            className="iocn-a"
            href="https://deri-protocol.medium.com"
            rel='noreferrer'
          >
            <Icon token='medium' width='16' height='16' />
          </a>
          <a
            target="_blank"
            href="https://twitter.com/DeriProtocol"
            rel='noreferrer'
          > <Icon token='twitter' width='16' height='16' /></a>
          <a
            target="_blank"
            className="iocn-a"
            href="https://github.com/deri-protocol"
            rel='noreferrer'
          >
            <Icon token='github' width='16' height='16' />
          </a>
          <a rel='noreferrer' target="_blank" className="iocn-a" href="https://t.me/DeriProtocol">
            <Icon token='telegram' width='16' height='16' />
          </a>
        </div>
        <div className='mobile'>
          <div className='banquan mobile-bn'>© {year} {lang['deri-protocal']}</div>
          <div className='contact mobil-con'>
            <a target="_blank" rel='noreferrer' href="mailto: service@deri.finance"><Icon token='email' width='16' height='16' /></a>
            <a
              target="_blank"
              className="iocn-a"
              href="https://deri-protocol.medium.com"
              rel='noreferrer'
            >
              <Icon token='medium' width='16' height='16' />
            </a>
            <a
              target="_blank"
              href="https://twitter.com/DeriProtocol"
              rel='noreferrer'
            > <Icon token='twitter' width='16' height='16' /></a>
            <a
              target="_blank"
              className="iocn-a"
              href="https://github.com/deri-protocol"
              rel='noreferrer'
            >
              <Icon token='github' width='16' height='16' />
            </a>
            <a rel='noreferrer' target="_blank" className="iocn-a" href="https://t.me/DeriProtocol">
              <Icon token='telegram' width='16' height='16' />
            </a>
          </div>
        </div>
      </div>
    </div>

  )
}
export default inject('intl')(observer(Footer))